@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box}#navbarContainer{background:#242121;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;min-height:60px;color:#fff;font-family:"Architects Daughter","sans-serif"}#navbarContainer a{text-decoration:none;color:#fff;height:100%;display:flex;align-items:center;justify-content:center;transition:.3s}#navbarContainer a:hover{color:#75da23}#navbarContainer #logo{margin-left:30px;font-size:2em;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}#navbarContainer #logoAndToggleDiv{display:flex;justify-content:space-between;align-items:center;min-height:60px}.sticky{position:fixed;top:0;width:100%}.mainNavbar{list-style-type:none;display:none;height:100%;padding-bottom:10px}.mainNavbar li{text-align:center;margin:10px 0 10px 0}.navbarToggle{cursor:pointer;font-size:24px;margin-right:30px}.active{display:block}@media screen and (min-width: 768px){#navbarContainer{display:flex;justify-content:space-between;padding-bottom:0;height:100px;align-items:center}#navbarContainer li{text-align:center}#navbarContainer .navbarToggle{display:none}.mainNavbar{display:flex;margin-right:30px;flex-direction:row;justify-content:flex-end;padding-bottom:0}.mainNavbar li{display:flex;align-items:center;justify-content:center;margin:0;border-bottom:3px solid #242121;transition:.3s}.mainNavbar li:hover{background-color:#3f3939;border-bottom:3px solid #75da23}.mainNavbar .navbarLinks{margin:0 10px 0 10px;min-width:58px}}#footer{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;min-height:150px;display:flex;justify-content:space-between;align-items:center;background:#242121;color:#fff;padding:10px}#footer #socialsContainer{display:flex;flex-direction:column;justify-content:flex-start;min-height:84px}#footer #socialsContainer h3{text-align:center}#footer #socialsContainer #socialsList{display:flex;align-items:flex-end;list-style:none;margin-top:5px}#footer #socialsContainer #socialsList li{margin:5px;display:flex;align-items:center}#footer #socialsContainer #socialsList li:hover{cursor:pointer}#footer .footerDivs{line-height:1.75em;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;margin:5px}#footer .footerDivs .phoneLink{text-decoration:none;color:#75da23}#footer .footerDivs .phoneLink:hover{text-decoration:underline;cursor:pointer}#footer .footerDivs .addressLink{text-decoration:none;color:#fff}#footer .footerDivs .addressLink:hover{text-decoration:underline;cursor:pointer}#footer .footerDivs p{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}#contentDiv{display:flex;flex-direction:column;align-items:center;justify-content:space-between;width:50vw;height:auto;line-height:1.75em;margin:20px}#contentDiv h1,#contentDiv p{margin:10px 0 10px 0}#homepage{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;min-height:100vh}#homepage #aboutUs{display:flex;justify-content:center;align-items:center;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;min-height:50vh;background-color:#eeebeb}#homepage #imageSlider{display:flex;justify-content:center;align-items:center;height:75vh;background-image:url(/tekela-restaurant-page/static/media/tacos-2.6ed1eef9.jpeg);background-repeat:no-repeat;background-position-x:10%;background-position-y:70%;background-size:cover;background-position:center}#homepage #imageSlider #linkDiv{display:flex;flex-direction:column;align-items:center;color:#fff;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;min-height:100px}#homepage #imageSlider #linkDiv #buttonDiv{display:flex;justify-content:space-between}#homepage #imageSlider #linkDiv #buttonDiv button{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;min-width:75px;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;min-height:40px;font-weight:200px;background-color:#d68426;color:#fff;border:none;transition:.3s;border:1px solid #fff;margin:20px 30px 10px 30px}#homepage #imageSlider #linkDiv #buttonDiv button:hover{cursor:pointer;background-color:#b36e1f}#homepage #imageSlider #linkDiv #viewMenusHeader{font-family:"Architects Daughter";font-size:55px}@media screen and (max-width: 768px){#homepage #imageSlider #linkDiv #viewMenusHeader{font-size:35px}}#contactForm{display:flex;flex-direction:column;justify-content:space-evenly;width:40vw;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;min-height:440px;min-width:310px;max-width:550px;padding:30px;margin:3% 0 3% 0;border:1px solid #6b6a6a}#contactForm label{display:flex;flex-direction:column;font-family:"Architects Daughter","sans-serif"}#contactForm textarea{max-width:100%;width:100%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;min-height:100px;resize:none;padding:5px}#contactForm input{line-height:1.2em;padding:5px}#contactForm h3{font-family:"Architects Daughter","sans-serif"}#contactForm button{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;min-height:30px;background:#d68426;border:none;color:#fff;min-width:100%;margin-top:10px}#contactForm button:hover{cursor:pointer;background-color:#b36e1f}#contactFormDiv{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;min-height:80vh;display:flex;flex-direction:column;align-items:center}#contactFormDiv #imageDiv{background:url(/tekela-restaurant-page/static/media/alcohol.369a301a.jpeg);background-repeat:no-repeat;background-position-y:70%;background-size:cover;height:60vh;width:100%;display:flex;justify-content:center;align-items:center}#contactFormDiv #contactHeader{font-size:80px;font-family:"Architects Daughter";color:#fff}@media screen and (max-width: 768px){#contactFormDiv #contactHeader{font-size:55px}}@media screen and (max-width: 768px){#footer{flex-direction:column;align-items:flex-start}#socialsContainer{align-items:flex-start}.footerDivs{padding:10px 5px 10px 5px}#contentDiv{width:70vw}}

